@import '../../constants';

.feed_back_wrapper {
    max-width: 1420px;
    display: flex;
    justify-content: space-between;
    gap: 40px;
    height: 225px;
    margin: 60px auto;
    padding: 0 10px;
}

.feed_back_title {
    font-size: 1.8rem;
    font-weight: 300;
    color: $black;
    text-transform: uppercase;
}

.social_wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.subscribe_wrapper {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
}

.social {
    width: 100%;
    height: 180px;
    background-color: $accent;
    display: flex;
    justify-content: center;
    align-items: center;

    &_link {
        width: 48px;
        height: 48px;
        display: flex;
        justify-content: center;
        align-items: center;

        @media (hover:hover) {
            &:hover {
                img {
                    transform: scale(1.3);
                }
            }
        }

        img {
            width: 24px;
            height: 24px;
            transition: 0.4s all ease;
        }
    }

    &_icons {
        flex: 0 1 480px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        min-width: 400px;
        margin: 0 46px;
    }
}

.subscribe {
    width: 100%;
    height: 180px;
    background-color: $black;
    display: flex;
    justify-content: center;
    align-items: center;
}

.subscribe_form {
    flex: 0 1 600px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 12px;
    gap: 20px;
    position: relative;
}

.subscribe_input {
    flex: 0 1 380px;
    height: 28px;
    color: $white;
    border-bottom: 1px solid $gray;
    transition: 0.4s all ease;

    &::placeholder {
        transition: 0.2s all ease;
        color: $gray;
        font-weight: 300;
        font-size: 1.4rem;
    }

    &:focus {
        &::placeholder {
            color: $white;
        }

        & {
            border-color: $white;
        }
    }
}

.subscribe_button {
    flex: 0 1 200px;
    height: 40px;
    background-color: $accent;
    border-radius: 100px;
    transition: 0.4s all ease;

    @media (hover:hover) {
        &:hover {
            background-color: $dimAccent;
        }
    }

    &:active {
        background-color: $dimAccent;
    }
}



@media screen and (max-width: 1000px) {
    .feed_back_wrapper {
        gap: 20px;
        margin: 40px auto;
    }

    .subscribe_form {
        flex-direction: column;
        max-width: 200px;
        
    }

    .subscribe_input {
        width: 100%;
        flex: 0 0 28px;
    }
    
    .subscribe_button{
        max-width: 200px;
        width: 100%;
        flex: 0 0 40px;
    }
}

@media screen and (max-width: 720px) {
    .feed_back_wrapper {
        flex-direction: column;
        height: max-content;
        gap: 30px;
        padding: 0;
    }

    .social_wrapper {
        flex: 0 0 215px;
    }

    .subscribe_wrapper {
        flex: 0 0 215px;
    }

    .feed_back_title {
        margin-left: 10px;        
    }

    .social {
        &_icons {
            justify-content: center;
            min-width: 225px;
            margin: 0 16px;
            gap: 12px;
            flex-wrap: wrap;
            flex: 1 1 225px;
        }
    }
}

@media screen and (max-width: 320px) {
    .feed_back_title {
        margin-left: 15px;   
    }

    .feed_back_wrapper {
        margin-top: 30px;
        margin-bottom: 50px;
    }
}